<template>
  <div v-if="goods" class="goods">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>商品管理</el-breadcrumb-item>
      <el-breadcrumb-item>商品列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
    <el-input @clear='searchGood' style="width:33% ; margin-top:20px" v-model="query.query" placeholder='请输入搜索内容' clearable>
      <el-button @click="searchGood" slot="append" icon="el-icon-search"></el-button>
    </el-input>
    <el-button type='primary'>添加商品</el-button>
    <el-table :data="goods.goods" border style="width: 100%">
      <el-table-column label="#" width="50"> </el-table-column>
      <el-table-column prop="goods_name" label="商品名称" width="580">
      </el-table-column>
      <el-table-column prop="goods_price" label="商品价格(元)" width="110">
      </el-table-column>
      <el-table-column prop="goods_weight" label="商品重量"> </el-table-column>
      <el-table-column prop="add_time" label="创建时间" width="180">
        <template v-slot="{ row }">
          {{ row.add_time | moment("YYYY-MM-DD HH:MM:SS") }}
        </template>
      </el-table-column>
      <el-table-column label="操作" width="120">
        <template v-slot="{ row }">
          <el-button
            type="primary"
            size="small"
            icon="el-icon-edit"
            @click="editDialog(row)"
          ></el-button>
          <el-button
            @click="del(row.goods_id)"
            type="danger"
            size="small"
            icon="el-icon-delete"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination
      @size-change="sizeChange"
      @current-change="currentChange"
      :page-sizes="[5, 10, 15, 20]"
      :page-size="10"
      layout="total, sizes, prev, pager, next, jumper"
      :total="goods.total"
    >
    
    </el-pagination>
    </el-card>
    <el-dialog title="编辑商品" :visible.sync="dialogFormVisible" width='80%'>
      <el-form :model="form" :rules="rules">
        <el-form-item prop='goods_name' label="商品名称">
          <el-input v-model="form.goods_name"></el-input>
        </el-form-item>
        <el-form-item prop='goods_price' label="价格">
          <el-input v-model="form.goods_price"></el-input>
        </el-form-item>
        <el-form-item prop='goods_number' label="数量">
          <el-input v-model="form.goods_number"></el-input>
        </el-form-item>
        <el-form-item prop='goods_weight' label="重量">
          <el-input v-model="form.goods_weight"></el-input>
        </el-form-item>
        
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="sureGoods"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapState } from "vuex";
export default {
  data() {
    return {
      query: {
        query: "",
        pagesize: 10,
        pagenum: 1,
      },
      dialogFormVisible:false,
      form:{

      },
      rules:{
        goods_name:[{required:true, message:'请输入商品名称',trigger:'blur'}],
        goods_price:[{required:true, message:'请输入价格',trigger:'blur'}],
        goods_number:[{required:true, message:'请输入数量',trigger:'blur'}],
        goods_weight:[{required:true, message:'请输入重量',trigger:'blur'}],

      },
      goodsId:'',

    };
  },
  computed: {
    ...mapState({
      goods: (state) => state.goods.goods,
    }),
  },
  created() {
    // const { pagesize, pagenum } = this;
    this.getGoods(this.query);
  },
  methods: {
    ...mapActions(["getGoods", "delGood","editGoodItem"]),
    // 每页多少条
    sizeChange(val) {
      this.query.pagesize = val;
      this.getGoods(this.query);
    },
    // 换页
    currentChange(val) {
      this.query.pagenum = val;
      this.getGoods(this.query);
    },
    del(id) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.delGood(id)
          .then(() => {
            this.$message({
              type: "success",
              message: "删除成功!",
            });
            this.getGoods(this.query)
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    editDialog(row){
      this.dialogFormVisible = true
      this.form={...row}
      this.goodsId=row.goods_id
    },
    sureGoods(){
      this.editGoodItem({id:this.goodsId,data:this.form})
      this.$message.success('修改成功')
      this.dialogFormVisible = false
    },
    searchGood(){
      this.getGoods(this.query)
    }
  },
};
</script>

